.main-preview__container {
	@include boxSize(100%, 457px);
	// @include boxSize(100%, 100%);
	@include gridContainer(
		grid,
		1fr,
		1fr 1fr 1fr 1fr,
		'resetBtn' 'userInfo' 'picture' 'socialMedia'
	);
	position: relative;
	background: $colorFromGrayToGhostwhite;
	padding: 38px 35px 40px 45px;

	@media all and (min-width: 768px) {
		@include boxParameter(0 0 0 30px, 173px 44px 260px 45px);
		max-width: 329px;
		min-height: 812px;
		border-radius: 6px;
	}
	@media all and (min-width: 1024px) {
		@include boxParameter(0 0 0 0px, 173px 140px 260px 180px);
		max-width: 585px;
		min-height: 812px;
		border-radius: 6px;
	}
}

.reset__btn {
	@include textParameter(
		$colorGraySlate,
		$fontFamilyCardButton,
		$fontSizeXXS,
		none
	);
	@include boxSize(75px, 30px);
	grid-area: resetBtn;
	border: 1px solid $colorBeige;
	border-radius: 15px;
	background-color: transparent;
	text-transform: uppercase;
	text-align: center;
}

.reset__icon {
	margin-right: 10px;
}

// COLORS PALETTES
.red-palette {
	color: $colorRedDriedBlood;
	// .card-user{
	//     border-left:5px solid #e95626;
	// }
	.card__item-list {
		border-color: $colorOrangeBlood;
	}
}
.gray-palette {
	color: $colorGraySwamp;
	// .card-user{
	//     border-left:5px solid #a0c0cf;
	// }
	.card__item-list {
		border-color: $colorSmokeBlue;
	}
}
.green-palette {
	color: $colorGreenBottle;
	// .card-user{
	//     border-left:5px solid #a2deaf;
	// }
	.card__item-list {
		border-color: $colorLimePastel;
	}
