//DIV BORDER
.borderLeftRed {
	border-left: 5px solid $colorOrangeBlood;
}
.borderLeftGreen {
	border-left: 5px solid $colorLimePastel;
}
.borderLeftGray {
	border-left: 5px solid $colorSmokeBlue;
}

.card__container {
	@include boxSize(240px, auto);
	@include flex(inherit, column, none, none);
	background-color: $colorBeige;
	box-shadow: $colorBoxShadow;
	text-align: start;
}

.card-user__container {
	@include boxSize(240px, 42px);
	grid-area: userInfo;
	padding-top: 23px;
	height: 75px;

	.card-user {
		//border-left: 5px solid $colorLimePastel;
		box-shadow: none;

		.card-user__name {
			@include textParameter(inherit, $fontFamilyCard, $fontSizeL, bold);
			margin: 0 0 4px 15px;
		}

		.card-user__job {
			@include textParameter(
				$colorGraySlate,
				$fontFamilyCard,
				$fontSizeM,
				normal
			);
			margin: 0 0 10px 15px;
		}
	}
}

.card__picture{
    @include boxSize(240px, 200px);
    display: inline-flex;
    grid-area: picture;
    box-shadow: $colorBoxShadow;
    background-size: cover;
    background-position: center;
    background-image: url('../images/darth-vader.jpg')
}

.card__social-media-list {
	@include flex(flex, flex-start, center, center);
	grid-area: socialMedia;
	position: absolute;
	bottom: 12px;
	margin-left: 20px;
}

.card__item-list {
	@include flex(flex, none, center, center);
	@include boxSize(32px, 32px);
	font-size: $fontSizeL;
	border: 2px solid $colorLimePastel;
	border-radius: 50%;
	margin-right: 10px;
}

.card__item-list-no-border {
	@include flex(flex, none, center, center);
	@include boxSize(32px, 32px);
	font-size: $fontSizeL;
	border: 0px;
	border-radius: 50%;
	margin-right: 10px;
}
.card__link {
	color: inherit;
}
